html {
  position: relative;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  min-width: 320px;
  min-height: 100vh;
  font-family: 'Lato', sans-serif;
  background: #fff;
  font-size: 20px;
  font-weight: 400;
  line-height: 120%;
}

img,
svg {
  max-width: 100%;
  max-height: 100%;
  height: auto;
}

button {
  padding: 0;
  cursor: pointer;
}

a {
  text-decoration: none;
  color: inherit;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

h1,
h2,
h3,
p,
figure,
fieldset {
  margin: 0;
}

iframe {
  border: none;
}

/*
Доступно скрываю элементы
*/

.visually-hidden {
  position: fixed;
  transform: scale(0);
}

/*
Пример контейнера
Если контейнер 1180px по макету,
то для max-width прибавляем паддинги 30px*2,
в итоге 1240px
Чтобы контент не прижимался к краям экрана
*/

.visually-hidden {
  position: fixed;
  transform: scale(0);
}

.container {
  max-width: 1920px;
  padding: 0 75px;
  margin: 0 auto;

  @media (max-width:1024px) {
    max-width: 1024px;
    padding: 0 42px;
  }

  @media (max-width:768px) {
    max-width: 768px;
    padding: 0 34px;
  }

  @media (max-width:320px) {
    max-width: 320px;
    padding: 0 20px;
  }
}
